.footer {
    background-color: #000;
    color: #fff;
    padding: 30px 0;
  }
  
  .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .section {
    margin-right: 30px;
  }
  
  .section h3 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .section p {
    margin: 0;
    margin-bottom: 10px;
  }
  
  .section ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .section ul li {
    margin-bottom: 5px;
  }
  
  .section ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  .section ul li a:hover {
    color: #a8a8a8;
  }
  
  .section form {
    margin-top: 10px;
  }
  
  .section form input[type="email"] {
    width: 100%;
    padding: 8px;
    border-radius: 5px;
    border: none;
    margin-bottom: 10px;
  }
  
  .section form button {
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 8px 15px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .section form button:hover {
    background-color: #45a049;
  }
  