.contactContent {
    background-color: #f6f6f6;
    flex-basis: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    width: 70%;
    text-align: center;
    margin: 5px  auto 30px auto;
  }
  
  .contactContent h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .contactContent p {
    font-size: 16px;
    margin-bottom: 20px;
  }
.contactSection {
    padding: 60px 0;
    /* background-color: #f8f8f8; */
  }
  
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
  }
  
  .form {
    /* background-color: #fff; */
    padding: 40px;
    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
  }
  
  .form h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 30px;
  }
  
  .form input,
  .form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form textarea {
    resize: none;
  }
  
  .form button {
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .map {
    position: relative;
    overflow: hidden;
    border-radius: 5px;
  }
  
  .map iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
  