/* About Us Section */
.aboutUsSection {
    padding: 5px;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.aboutContent {
    background-color: #f6f6f6;
    flex-basis: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    width: 70%;
    text-align: center;
    margin: 0 auto;
  }
  
  .aboutContent h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .aboutContent p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .aboutBtn {
    display: inline-block;
    background-color: #f44236;
    color: #ffffff;
    padding: 10px 30px;
    text-decoration: none;
    border-radius: 30px;
  }
  .aboutImage {
    display: flex;
    justify-content: space-around;
    width: 70%;
    align-items: center;
    margin: 0 auto;
    margin-top: 50px;
  }
  .whoWe {
    margin-left: 90px;
  }
  .aboutImage img {
    width: 100%;
    max-width: 800px;
  }
  
  /* Newsletter Section */
  .newsletterSection {
    background-color: #ffffff;
    padding: 80px 0;
    text-align: center;
  }
  
  .newsletterSection h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .newsletterSection p {
    font-size: 16px;
    margin-bottom: 30px;
  }
  
  .newsletterInput {
    width: 300px;
    /* height: 50px; */
    padding: 10px;
    border: 1px solid #cccccc;
    border-radius: 30px;
    margin-right: 10px;
  }
  
  .newsletterBtn {
    display: inline-block;
    background-color: #f44236;
    color: #ffffff;
    padding: 10px 30px;
    text-decoration: none;
    border-radius: 30px;
    border: none;
    cursor: pointer;
  }
  
  .newsletterBtn:hover {
    background-color: #d92d1b;
  }
  