.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url("https://www.sales-i.com/hubfs/Imported_Blog_Media/crm-2013-1.jpg");
  background-size: cover;
}

.form {
  width: 400px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 30px;
}

.form h3 {
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 30px;
}

.form label {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
}

.form input {
  display: block;
  height: 40px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 0 10px;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 300;
  outline: none;
}

.form ::placeholder {
  color: #ccc;
}

.form button {
  width: 100%;
  background-color: #2196f3;
  color: #fff;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin-bottom: 10px;
  transition: background-color 0.3s ease;
}

.form button:hover {
  background-color: #1976d2;
}

.form a {
  color: #4caf50;
  text-align: center;
  display: block;
  margin-top: 20px;
}

.warning {
  display: none;
  background-color: #ff5252;
  color: #fff;
  padding: 8px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: center;
}

.mailWarning {
  display: none;
  background-color: #ff5252;
  color: #fff;
  padding: 8px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: center;
}

.passwordWarning {
  display: none;
  background-color: #ff5252;
  color: #fff;
  padding: 8px;
  border-radius: 5px;
  margin-bottom: 20px;
  text-align: center;
}

