.container {
  margin-top: 20px !important;
}

.product {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.product h3,
.product p {
  margin: 0;
}

.product img {
  width: 90%;
  height: 50%;
}

.emptyCart {
  color: #f44236;
  text-align: center;
  margin-top: 200px;
}

.up {
  height: 30%;
}

.down {
  height: 10%;
}

.btnsContainer {
  display: flex;
  justify-content: space-between;
}

#payBtn,
#deleteBtn {
  background-color: #f44236;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  width: 48%;
  padding: 8px 0;
  border: none;
}

#payBtn:hover,
#deleteBtn:hover {
  background-color: #d92d1b;
}

.delivery {
  color: green;
}
