.sale {
    display: flex;
    justify-content: center;
    margin: 100px;
    height: 300px;
}
.super {
    background-image: url(../assets/images/sale1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    padding-left: 30px;
    padding-top: 10px;
    width: 452px;
    height: 346px;
    background-size: 100%;
    transition: all 1.2s ease-in-out;
}
.super:hover {
    background-size: 104%;
}

.season {
    background-image: url(../assets/images/sale2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 50px;
    width: 641px;
    height: 236px;
    padding-left: 50px;
    padding-top: 120px;
    background-size: 100%;
    transition: all 1s ease-in-out;
}
.season:hover {
    background-size: 104%;
}

h3 {
    font-weight: 700;
}

.navLink {
    text-decoration: none;
    color: inherit;
}